</template>

<script>
//
import MovieList from './components/MovieList.vue'
//
import MovieDetail from './components/MovieDetail.vue'
export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
    }
  },
  computed:{
    bottomBtn(){
      return{
        bottomBtn:true,
      }
    },
  },
  components:{
    //
    MovieDetail,
    //
    MovieList,
  },
}
</script>

<style>
body,html{padding: 0;margin: 0;font-size:16px;}
/*

 */
.loading{
  text-align: center;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2000;
  background-color: rgba(0,0,0,0.4);
}



</style>

<style scoped>
.bottomBtn a{
  color:#111;
  text-decoration: none;
}
.bottomBtn.active a{
    color:#26a2ff;
}

</style>





